@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-nueva-tarea{
   
        display: flex;
        justify-content: center;
     

    .agregar-tarea{
        @include m.boton(v.$cyan, v.$blanco);
        border-radius: .5rem;
        
    }
    
}
    
.listado-tareas{
    list-style: none;
    padding: 0;
    margin-top: 3rem;
    .no-tareas{
        text-align: center;
    }
}

.tarea{
    padding: .5rem 2rem;
    border-bottom: 2px solid darken(v.$grisClaro, 5%);
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &:last-of-type{
        border-bottom: none;
    }

    p{
        font-size: 1.4rem;
    }

    .opciones{
        display: flex;
        gap: 1rem;
        button{
            border: none;
            font-size: .9rem;
            padding: .5rem 1rem;
            text-transform: uppercase;
            font-weight: 700;
            border-radius: .7rem;

            &:hover{
                cursor: pointer;
            }

        }
        .completada{
            background-color: v.$azul;
            color: v.$blanco;

            &:hover{
                background-color: darken(v.$azul, 10%);
            }

        }
        .pendiente{
            background-color: v.$naranja;
            color: v.$blanco;
            &:hover{
                background-color: darken(v.$naranja, 10%);
            }
        }
        .eliminar-tarea{
            background-color: v.$rojo;
            color: v.$blanco;
            &:hover{
                background-color: darken(v.$rojo, 10%);
            }
        }
    }
}