@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra-mobile{
    background: linear-gradient(to right, v.$indigo 0%, lighten(v.$cyan, 10%) 100%);
    padding: 2rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include m.tablet { 
        display: none;
     }

     h1{
        color: v.$blanco;
        margin: 0;
     }

     .menu{

        .mobile-menu{
            width: .8rem;
            &:hover{
                cursor: pointer;
            }
        }
     }
}

.barra{
    
    display: none;
    @include m.tablet { 
        background-color: v.$blanco;
        padding: 1rem 2rem;
        box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, .4);
        display: flex;
        justify-content: space-between;
        align-items: center;
     }

    p{
        margin: 0;
        font-size: 1.4rem;
        span{
            color: v.$indigo;
            font-weight: 700;
        }
    }

    a{
        background: linear-gradient(to right, v.$indigo 0%, lighten(v.$cyan, 10%) 100%);
        color: v.$blanco;
        border-radius: .6rem;
        padding: .5rem 2rem;
        transition-property: background;
        transition-duration: 300ms;
    
        &:hover{
            color: v.$rojo;
        }
    }
}